@font-face {
    font-family: 'Seagram tfb';
    src: url('Images/Seagram tfb.ttf');
}

* {
    margin: 0;
    padding: 0;
}

:root {
    --clr1: #313131;
    --clr2: #C90000;
}

body {
    max-width: 1650px;
    margin: 0 auto;
    background-color: var(--clr1);
}



.container {
    /*    position: absolute;*/
    /*    margin: 0 auto;
    max-width: 1040px;*/
    max-width: 1650px;
    margin: 0 auto;
    background-color: var(--clr1);
}

.banner {
    margin: 0;
    height: 200px;
}

a {
    text-decoration: none;
}

ul {
    list-style-type: none;
}

.navbar {
    /* width: 1200px; */
    width: 1650px;
    margin-left: 15em;
}

.navbar ul {
    display: block;
    margin: 0;
    padding: 0;
}

li {
    float: left;
}

.navbar li h3 {
    color: white;
    background-color: var(--clr2);
    border: 1px solid black;
    display: block;
    text-align: center;
    padding: 16px;
    font-family: Seagram tfb;
    font-size: 24px;
    letter-spacing: 3px;
}

.navbar li h3:hover {
    background-color: #9B0000;
    cursor: pointer;
}

.main {
    display: block;
    clear: both;
    background-color: var(--clr1);
}

.main h2 {
    margin-left: 8em;
    font-family: Seagram tfb;
    color: white;
    font-size: 32pt;
    letter-spacing: 3px;
    text-align: left;
}

.main pre {
    font-family: Lucida Console;
    font-size: 16pt;
    letter-spacing: 2px;
    color: white;
    margin-left: 15em;
    line-height: 1.2;
}


